import React from 'react';
import autoBind from 'react-autobind';
import { Spinner } from '../module/Spinner';
import { DataNotFound } from '../module/DataNotFound';
import { Item } from './Item';
import { store } from '../../reducers';
import VisiblePagination from '../../containers/VisiblePagination.js';

class List extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
  }

  componentWillMount() {

  }

  show() {
    if (this.props.code === 100) {
      return (<Spinner />);
    } else if (this.props.code === 200) {
      return (
        <div>
          <h4>{this.props.title}</h4>
          {this.props.articles.data.map(article => (
            <Item key={article.id} article={article} />))}
        </div>
      );

    } else {
      return (<DataNotFound />);
    }
  }

  render() {
    return (
      <div>
        {this.show()}
        <VisiblePagination store={store} />
      </div>
    );
  }
}
export { List }



